.collection {
  margin: 0 auto 105px;

  &__title {
    color: $color-title;
    font-size: 35px;
    line-height: 51px;
    padding-bottom: 25px;
    margin-bottom: 25px;
    position: relative;
    text-align: center;
    @extend %title-font;
    &::after {
      position: absolute;
      content: "";
      width: 250px;
      height: 1px;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0 auto;
      background-color: #6d7278;
    }
  }

  &__text {
    //margin-bottom: 30px;
    max-width: 730px;
    margin: 0 auto 30px;
  }

  &__items {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
  }
  &__item {
    position: relative;
  }
  &__img {
    min-height: 350px;
    object-fit: cover;
  }
  &__info {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#fff, 0.7);
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all ease 0.7s;
    &:hover {
      opacity: 1;
      transition: opacity ease 0.7s;
    }
  }
  &__info-title {
    font-size: 20px;
    font-weight: 700;
    line-height: 25px;
    color: $color-title;
    padding-bottom: 19px;
    margin-bottom: 19px;
    position: relative;
    @extend %title-font;
    &::after {
      content: "";
      position: absolute;
      width: 80%;
      height: 1px;
      left: 0;
      right: 0;
      bottom: 0;
      margin: 0 auto;
      background-color: #6d7278;
    }
  }

  &__info-text {
    font-weight: 600;
    font-size: 14px;
    color: #707070;
  }
}
